	module.exports = {"data":{"post":{"html":"<p><a href=\"http://www.flickr.com/photos/papascott/464832198/\" title=\"Photo Sharing\"><img src=\"http://farm1.static.flickr.com/195/464832198_d1abe957bc.jpg\" width=\"500\" height=\"375\" alt=\"IMG_1976\" /></a></p>\n<p>We have a name. And a sign. McDonald's doesn't want franchisees to use 'Mc' or any trademarks or product names when naming their companies, so fun names like 'McMaus' are out. So we've gone for something boring and practical. And yes, we know the sign has typos. It's just a first draft.</p>\n<p>Meanwhile MamaMaus has visited a consultant and a bookkeeping service (husband and wife, actually), has been to two banks and the local *Wirtschaftsförderung* (business development) office, and has appointments next week with a lawyer and a tax advisor. Meanwhile she's still working on showing her successor around the region, taking day trips to places like Kiel, Göttingen, Braunschweig and Oldenburg. The handover will take another 3 weeks or so.</p>\n<p>We're still waiting for a franchise contract and a concrete purchase price. MamaMaus has negotiated such sales in the past, though, so we have a good a idea how much it will be. And we've got our first bit of capital sitting liquid in a cash management account.</p>","fields":{"title":"GmbH und Co KG","date":"19 Apr 2007","url":"https://papascott.de/2007-04-19-gmbh-und-co-kg/"}}},"pathContext":{"slug":"/2007-04-19-gmbh-und-co-kg/","prev":{"fields":{"title":"Most Socially Responsible?","date":"18 Apr 2007","slug":"/2007-04-18-most-socially-responsible/"}},"next":{"fields":{"title":"T-Shirt","date":"20 Apr 2007","slug":"/2007-04-20-t-shirt/"}}}}

